body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

p {font-family: Arial, Helvetica, sans-serif;}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.flex {display: -webkit-flex;display: flex;}
.flex-wrap {-webkit-flex-wrap: wrap;flex-wrap: wrap;}
.no-wrap {-webkit-flex-wrap: nowrap;flex-wrap: nowrap;}
.flex-column {-webkit-flex-direction: column;flex-direction: column;}
.justify-center {-webkit-justify-content: center;justify-content: center;}
.justify-start {-webkit-justify-content: flex-start;justify-content: flex-start;}
.justify-end {-webkit-justify-content: flex-end;justify-content: flex-end;}
.justify-space-around {-webkit-justify-content: space-around;justify-content: space-around;}
.justify-space-between {-webkit-justify-content: space-between;justify-content: space-between;}
.align-center {-webkit-align-items: center;align-items: center;}
.align-start {-webkit-align-items: flex-start;align-items: flex-start;}
.align-end {-webkit-align-items: flex-end;align-items: flex-end;}
.flex-1 {-webkit-flex: 1 1;flex: 1 1;}
.flex-auto {-webkit-flex: auto;flex: auto;}

/* FLOAT */
.f-right {float: right;}
.f-left {float: left;}

/* POSITION */
.relative {position: relative;}
.absolute {position: absolute;}
.sticky {position: -webkit-sticky;position: sticky;}

.right-0 {right: 0;}
.left-0 {left: 0;}
.top-0 {top: 0;}
.bottom-0 {bottom: 0;}

.right-1 {right: 1em;}
.left-1 {left: 1em;}
.top-1 {top: 1em;}
.bottom-1 {bottom: 1em;}


/* FULLBOX */
.full {width: 100%; height: 100%;}

/* CENTER IN ABSOLUTE */
.ab-center {top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%);}




/* SHAPE */
.circle {
  border-radius: 50%;
}
.triangle-right {
  width: 0px;
  border: 16px solid;
  border-color: inherit;
  border-top-color: transparent;
  border-bottom-color: transparent;
  border-right-color: transparent;
}
.triangle-left {
  width: 0px;
  border: 16px solid;
  border-color: inherit;
  border-top-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.triangle-down {
  width: 0px;
  border: 16px solid;
  border-color: inherit;
  border-bottom-color: transparent;
  border-left-color: transparent;
  border-right-color: transparent;
}
.triangle-up {
  width: 0px;
  border: 16px solid;
  border-color: inherit;
  border-top-color: transparent;
  border-left-color: transparent;
  border-right-color: transparent;
}



